.root {
  width: 100vw;
  padding-top: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
  /* height: 100vh; */
}
//.container {
//  max-width: 1200px;
//}
.all_height {
  max-height: 100vh;
}
.left_page {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.info_image {
  width: 480px;
}
.title {
  font-size: 24px;
  font-weight: 400;
  color: #333;
  margin-top: 50px;
}
.desc {
  font-size: 16px;
  color: #b0b0b0;
}
.price {
  height: 40px;
  display: flex;
  align-items: center;
  box-sizing: border-box;
  padding-left: 20px;
  font-size: 18px;
  font-weight: 400;
  background: linear-gradient(to right, #ee692d, #eb5440);
  color: #fff;
}
.banben_title {
  font-size: 20px;
  font-weight: 400;
  color: #333;
}
.color_title {
  font-size: 20px;
  font-weight: 400;
  color: #333;
  margin-top: 20px;
}
.total {
  width: 100%;
  box-sizing: border-box;
  padding: 20px;
  margin-top: 20px;
  background-color: #f9f9f9;
}
.total_desc {
  font-size: 16px;
  color: #616161;
  margin-bottom: 10px;
}
.total_money {
  font-size: 24px;
  color: #ff6700;
  font-weight: 400;
  margin-bottom: 0;
}

.button {
  margin-top: 20px;
}
.buttonGrop {
  width: 100%;
}

.review{
  width: 1024px;
  h3{
    background: linear-gradient(to right, #ee692d, #eb5440);
    color:white;
  }
  .text_review{
    display: block;
    width: 1024px;
    height: 100px;
    font-size: 16px;
  }
  button{
    float: right;
    display: block;
  }
  hr{
    border:1px dotted #eb5440;
    display: block;
  }
  .comment{
    h4{
      color:#eb5440;
    }
  }
}